<template>
  <div id="app">

      <div class="form-group row">
        <input placeholder="R" type="text" class="form-control col-4 mx-2"  v-model="forSearch.R">
        <input type="text" class="form-control col-4 mx-2" placeholder="x" v-model="forSearch.x">
        <input type="text" class="form-control col-4 mx-2" placeholder="y" v-model="forSearch.y">
        <input type="text" class="form-control col-4 mx-2" placeholder="K" v-model="forSearch.K">
        <button v-on:click="search" class="btn btn-success" > Srch/All </button>

      </div>


    <form @submit="putDot">
      <div class="form-group row">
        <input readonly placeholder="pk" type="text" class="form-control col-4 mx-2"  v-model="item.pk">
        <input type="text" class="form-control col-4 mx-2" placeholder="x" v-model="item.x">
        <input type="text" class="form-control col-4 mx-2" placeholder="y" v-model="item.y">
        <input type="text" class="form-control col-4 mx-2" placeholder="description" v-model="item.desc">
        <button class="btn btn-success"> Put </button>

      </div>
    </form>
    <table class="table">
      <thead>
      <th>x</th>
      <th>y</th>
      <th>description</th>
      </thead>
      <tbody>
      <tr v-for="item in items" :key="item.pk" @dblclick="$data.item=item">
        <td> {{ item['x'] }}</td>
        <td> {{ item['y'] }}</td>
        <td> {{ item['desc'] }}</td>
        <button class="btn btn-danger" @click="deleteDot(item.pk)"> x </button>
      </tr>
      </tbody>
    </table>
  </div>
</template>
<script>


export default {
  name: 'App',
  data: function() {
    return {
      item: {
        'pk':'',
        'x':'',
        'y':'',
        'desc':''
      },
    forSearch:{
        'R':'',
        'x':'',
        'y':'',
        'K':''
    },
      count: 0,
      items:  []
    }
  },
  async created(){
    await this.getall();
  },

  methods:{

     async getall(){
      var url= 'http://127.0.0.1:8000/poligon/dots/'
       var response = await fetch(url,{
        method: 'get',
        headers: {"Content-type": "application/json"}});
       this.items= await response.json();
    },
    async putDot(){

      if( this.item.pk=='') {
        await this.createDot();
      } else {
        await this.updateDot();
      }

    },
    async createDot(){
      var response = await fetch('http://127.0.0.1:8000/poligon/dots/',{
        method: 'post',
        headers: {
          'Content-Type':'application/json'
        },
        body: JSON.stringify(this.item)
      });
      this.items.push(response.json());

    },
    async updateDot(){
      var response = await fetch('http://127.0.0.1:8000/poligon/dot/'+ this.item.pk,{
        method: 'put',
        headers: {
          'Content-Type':'application/json'
        },
        body: JSON.stringify(this.item)
      });
      console.log(response);
      await this.getall();
    },

    async deleteDot(pk){
      await fetch('http://127.0.0.1:8000/poligon/dot/'+pk,{
        method: 'delete',
        headers: {
          'Content-Type':'application/json'
        },
        body: JSON.stringify(this.item)
      });
      await this.getall();
    },

     async search(){
       if( this.forSearch.K!='' & this.forSearch.x!='' & this.forSearch.y!='' & this.forSearch.R!='' ) {
         await fetch('http://127.0.0.1:8000/poligon/dotfromR/', {
           method: 'POST',
           headers: new Headers({
             'Content-Type': 'application/json'
           }),
           body: JSON.stringify(this.forSearch)
         }).then(async (response) => {
           var a = await response.json();
           console.log(a);
           this.items = a;
         }).catch((err) => console.error(err.toString()));
       }else {
         await this.getall()
       }
    }
  }
}
</script>


<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
